import React, { useRef } from 'react';
import './Header.css';
import { Link } from 'react-scroll'

export default function HeaderComponent () {

  const titleRef = useRef()
  function clickHandler() {
    console.log('button clicked')
    titleRef.current.scrollIntoView({ behavior: 'smooth' })
  }

  return(

    
    <div className="Header-component">
        <div className="Tool-button" onClick={clickHandler}><Link  to="about" spy={true} activeClass="active">About</Link></div>
        <div className="Tool-button" onClick={clickHandler}><Link  to="projects" spy={true} activeClass="active">Projects</Link></div>
        <div className="Tool-button" onClick={clickHandler}><Link  to="contact" spy={true} activeClass="active">Contact</Link></div>
    </div>
  )
}